.kwizzad-modal-dialog-iframe .kwizzad-modal-dialog-content {
  position: relative;
  width: 90%;
  height: 90%;
  -webkit-box-shadow: 0 1px 2px rgba(0,0,0,0.3), 0 10px 30px rgba(0,0,0,0.15);
          box-shadow: 0 1px 2px rgba(0,0,0,0.3), 0 10px 30px rgba(0,0,0,0.15);
}

@media (min-width: 1200px) {
  .kwizzad-modal-dialog-iframe .kwizzad-modal-dialog-content {
    max-width: 1200px;
    max-height: 768px;
  }
}
.kwizzad-modal-dialog-iframe .kwizzad-modal-dialog-content iframe {
  position: relative;
  width: 1px;
  min-width: 100%;
  height: 100%;
}

.kwizzad-modal-dialog-iframe .kwizzad-modal-dialog-content {
  -webkit-transition: -webkit-transform 0.3s ease-out;
  transition: -webkit-transform 0.3s ease-out;
  -o-transition: transform 0.3s ease-out;
  transition: transform 0.3s ease-out;
  transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
  -webkit-transform: scale3d(0.9, 0.9, 0.9);
          transform: scale3d(0.9, 0.9, 0.9);
}

.kwizzad-modal-dialog-iframe .kwizzad-modal-dialog-fullscreen-overlay {
  -webkit-transition: background-color 4s ease-out, -webkit-transform 1s ease-out;
  transition: background-color 4s ease-out, -webkit-transform 1s ease-out;
  -o-transition: background-color 4s ease-out, transform 1s ease-out;
  transition: background-color 4s ease-out, transform 1s ease-out;
  transition: background-color 4s ease-out, transform 1s ease-out, -webkit-transform 1s ease-out;
  background-color: rgba(0,0,0,0);
  -webkit-transform: scale3d(0.9, 0.9, 0.9);
          transform: scale3d(0.9, 0.9, 0.9);
}

.kwizzad-container-show .kwizzad-modal-dialog-content {
  -webkit-transform: scale3d(1, 1, 1);
          transform: scale3d(1, 1, 1);
}

.kwizzad-container-show .kwizzad-modal-dialog-iframe .kwizzad-modal-dialog-fullscreen-overlay {
  background-color: rgba(0,0,0,0.7);
  -webkit-transform: scale3d(1, 1, 1);
          transform: scale3d(1, 1, 1);
}


